  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.button {
  display: block;
  min-width: 180px;
  padding-top: 13px;
  padding-right: 15px;
  padding-bottom: 14px;
  padding-left: 15px;
  font-size: 14px;
