  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px;
  cursor: pointer; }
  .button:hover, .button:focus {
    opacity: 0.8;
    outline: none; }
  .button:active {
    opacity: 0.6; }
  .button_theme_fill {
    min-width: 407px;
    padding-top: 17px;
    padding-bottom: 18px;
    font-size: 16px;
    line-height: 19px;
    background-color: #00B2FF;
    border-color: #00B2FF; }

.container {
