  margin-left: 9.84848%;
  line-height: 22px;
  color: #ffffff; }
  .header__link:hover, .header__link:focus {
    opacity: 0.8;
    outline: none; }
  .header__link:active {
    opacity: 0.6; }
  @media (min-width: 1024px) {
